    info: 'Один из самых распространенных видов живописи сегодня - это масляная живопись. В средние века широкую популярность она получила благодаря нидерландскому художнику Яну Ван Эйку. Большая часть исследователей его творчества считает, что именно он основоположник этой техники рисования. Живопись маслом, как понятно из названия, – это рисование масляными красками, которые состоят из высыхающего масла (чаще всего льняного, масла мака или грецкого ореха) и сухого пигмента. Пигменты в масляных красках бывают лессирующие (прозрачные) – они создают ощущение глубины на рисунке, и укрывистые (покрывные, непрозрачные). Лучшие краски составляются по условной формуле «1 краска = 1 пигмент в максимальной концентрации», но часто мастера для получения нужного оттенка смешивают краску из нескольких пигментов на палитре. Особенно часто такой прием используется, если выполняются маслом картины цветов либо пейзажи. Что касается собственно самого масла – то чаще всего пользуются льняным маслом холодного отжима, оно золотистого цвета и не имеет запаха. Для белого пигмента часто используют абсолютно прозрачное маковое масло. После нанесения на картину масляная краска за несколько дней подсыхает и превращается в прозрачную водостойкую пленку, очень, однако, хрупкую, трескающуюся при малейшем изгибе. Процесс окончательного засыхания масляной краски длится около 2-3 лет, причем наблюдается одна интересная деталь – картина в это время существенно прибавляет в весе, а затем снова становится легче. Льняное масло желтеет при отсутствии света, поэтому после долгого хранения картины в шкафу или в запасниках музея для возвращения ей первоначального вида её нужно выставить на яркий солнечный свет на несколько дней. Чаще всего масляными красками рисуют на холсте – льняном или хлопковом или на деревянной дощечке, а живопись маслом для начинающих – это рисование на картоне. В любом случае, если говорят «картина-холст-масло», то подразумевают, что сначала на основание (холст или другой материал) наносят грунт – он препятствует растеканию масляной краски по картине. Но все же основной материал тут – холст, и купить картину маслом на холсте – это более престижно, чем просто купить картину маслом.',
