import React from 'react';
import AspectRatio from '@mui/joy/AspectRatio';
import Button from '@mui/joy/Button';
import Card from '@mui/joy/Card';
import CardContent from '@mui/joy/CardContent';
import IconButton from '@mui/joy/IconButton';
import Typography from '@mui/joy/Typography';
import BookmarkAdd from '@mui/icons-material/BookmarkAddOutlined';
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import { useMediaQuery } from '@mui/material';
import { setCloseInfo } from './redux/slices/OpenImageInfo.slice';
import { useDispatch } from 'react-redux';

function ImageInfo({ title, image, year, price, info, id }) {
  const isSmallScreen = useMediaQuery('(max-width:900px)');
  const contentWidth = isSmallScreen ? '281px' : '550px';
  const contentLeft = isSmallScreen ? '34px' : '149px';
  const typographySize = isSmallScreen ? '15px' : '30px';
  const typographyDateSize = isSmallScreen ? '15px' : '17px';
  const dispatch = useDispatch();

  return (
    <div className="Image-Info-Main">
      <img
        onClick={() => dispatch(setCloseInfo({ id }))}
        className="back-icon"
        src="data:image/png;base64,"
      />
      <Card
        className="media-Info-Main"
        variant="outlined"
        sx={{ position: 'fixed', width: contentWidth, left: contentLeft, top: '149px' }}>
        <div>
          <Typography level="title-lg" sx={{ fontSize: typographySize }}>
            {title}
          </Typography>
          <Typography level="body-sm" sx={{ fontSize: typographyDateSize }}>
            {year}
          </Typography>
        </div>
        <AspectRatio minHeight="300px" maxHeight="500px">
          <img src={image} loading="lazy" alt="" />
        </AspectRatio>
        <CardContent orientation="horizontal">
          <div>
            <Typography level="body-xs">Цена:</Typography>
            <Typography fontSize="lg" fontWeight="lg" sx={{ fontSize: '23px' }}>
              ${price}
            </Typography>
          </div>
          <Button
            variant="solid"
            size="md"
            color="primary"
            aria-label="Explore Bahamas Islands"
            sx={{ ml: 'auto', alignSelf: 'center', fontWeight: 600 }}>
            читать информацию{' '}
            {window.innerWidth <= 650 ? <ArrowUpwardIcon /> : <ArrowRightAltIcon />}
          </Button>
        </CardContent>
      </Card>

      <div className="Info-Main">
        <p>{info}</p>
      </div>
    </div>
  );
}

export default ImageInfo;
