    price: 700000,
    info: 'Это самый авангардный прорыв в изобразительном искусстве 19-го века, настоящая революция, которая, так уж получилось, была названа по имени  этой совсем небольшой (48 х 63 см) и, на первый взгляд, совершенно невзрачной картины Моне, название которой в оригинале выглядит следующим образом: Impression, soleil levant (фр.) Сегодня слово "импрессионизм" давно обрело культовый статус. Стоит произнести его, и в воображении, окутанном романтической дымкой - совсем как на картине "Впечатление" Клода Моне - тут же возникает веселая и беззаботная ватага французских художников, прожигавших жизнь в увеселительных заведениях, водивших дружбу с проститутками, не имевших за душой ни единого су - и писавших при этом восхитительные полотна, ниспровергавшие напрочь весь скучный академический вздор, который в 19-ом веке все еще продолжали называть живописью.  Продолжали, понятно, по инерции - поскольку живописи там давно уж не осталось: только мертвое ремесло, общественное признание, денежные заказы и страх нарушить хотя бы на полшага заданные предшественниками и одобренные общественностью рамки.  У Моне, Мане, Сислея или Дега ничего этого не было: ни денег, ни признания, ни, тем более, страха. Боятся лишь те, кому есть, что терять. Как истинные революционеры от искусства, эти молодые люди, патологически не желавшие становиться частью системы, которую они осознанно отвергли и предали, упорно пытались проторить в искусстве свою собственную стезю - и, агрессивно противопоставив себя традиции, вряд могли рассчитывать на теплый прием.   Прием и не был - теплым. Вердикт как публики, так и коллег по цеху, был однозначным: бездарная мазня и барахло. Шлак. Отстой - используя более близкие нашему времени категории. Это сегодня при слове "импрессионизм" сразу пахнет абсентом, гениальностью и Парижем - а в те времена, когда все начиналось, от него несло сточной ямой. Слово импрессионизм, между прочим, было придумано как ругательство - если совсем уж придерживаться исторической правды.   Изобрел этот оскорбительный термин Луи Леруа - не самый талантливый французский драматург и журналист. 25 апреля 1874 года в сатирическом журнале Le Charivari, с которым сотрудничал Леруа, была опубликована его статья, посвященная первой выставке "тех самых французов" -  Моне, Дега, Писарро, Ренуара и еще трех десятков "молодых ниспровергателей".  В этой статье,  выдержанной в негативно-сатирическом тоне, Леруа, истекая праведной желчью, окрестил восставших против традиции художников "импрессионистами" ("впечатленцами"), взяв за основу название картины Моне "Впечатление. Восход солнца". Язвительный критик даже помыслить не мог, что войдет в историю искусства не из-за своих бездарных пьес или фельетонов, но исключительно благодаря придуманному им "ругательству". Те, кого он пытался оскорбить, не стали, вопреки ожиданиям,  открещиваться от нового имени и приняли оскорбление, как награду, с той поры начав именовать себя импрессионистами.  Понятно, что картине "Впечатление. Восходящее солнце", оказавшейся в эпицентре негативного внимания, досталось больше всего. Это живопись? Да вы, должно быть, шутите - даже обойная бумага смотрится лучше! В крайнем случае, эту безобразную серую мазню можно назвать этюдом - но уж никак не картиной! Эдак всякий, кто купит краски и кисть, может сразу назвать себя гениальным художником - раз уж для этого не нужно учиться рисовать!  Рисовать Клод Моне умел. А еще он, к счастью, обладал совершенно непробиваемым защитным панцирем, от которого любая критика отскакивала, как вишневые косточки от танка. Клод Моне, в определенном смысле, и был танком - тяжеловесным упрямым сыном гаврского бакалейщика, который шел к своей цели: рисовать так, как он считает нужным - слишком  трудно и чересчур долго, чтобы какие-то критики могли заставить его усомниться в правильности избранного пути.  Моне не то чтобы недолюбливал критиков - он вообще не принимал их в расчет. Если на то пошло, он и всех людей в целом считал довольно сомнительным экспериментом природы - и потому преподчитал не занимать ими пространство своих работ - за редкими исключениями. Гораздо сильнее ему нравились менее одушевленные вещи - например, кувшинки, или порт родного Гавра, изображенный им в картине "Впечатление. Восходящее солнце". ',
